// pages/example/example.js
const app = getApp();
const apiUrl = app.getApiUrl();
const imgUrl = app.getImgUrl();
const fadan = require('../../utils/fadan.js');

Page({

  /**
   * 
   */
  data: {
    imgUrl: imgUrl,
    exampleArr:[],
    currentPage: 1,
    hasData: 1,
    scrollContainerHeight: '1455rpx',
    isUpdateScrollHeight: false,
    categoryNoData: 1, 
    personNum:[]
  },

  /**
   * --
   */
  onLoad: function (options) {
    let that = this;
    fadan.fadan.init(that, 3, {
      cityInput: true,
      addressInput: false,
      phoneInput: true,
      nameInput: true,
      areaInput: false,
      btnText: "",
    });
    wx.request({
      url: apiUrl + '/company/caselist?',
      data: {
        bm: options.bm,
        id: options.id,
        p: '1',
        pagecount: 15
      },
      header: {
        'content-type': 'application/json'
      },
      success: function (res) {
        let arr = [];
        for(let i = 0;i < res.data.data.length;i++){
          res.data.data[i].personNum = app.getPersonNum();
          arr[i] = app.getPersonNum()
        }
        wx.setStorage({
          key:"personNum",
          data: arr
        });
        that.setData({
          exampleArr: res.data.data,
        })
      }
    })
  },

  /**
   * --
   */
  onReady: function () {
  
  },

  /**
   * --
   */
  onShow: function () {
   
  },

  /**
   * --
   */
  onHide: function () {
  
  },

  /**
   * --
   */
  onUnload: function () {
  
  },

  /**
   * --
   */
  onPullDownRefresh: function () {
  
  },

  /**
   * 
   */
  onReachBottom: function () {
  
  },

  /**
   * 
   */
  onShareAppMessage: function (ops) {
    if (ops.from === 'button') {
      // 
      console.log(ops.target)
    }
    return {
      title: '',
      path: 'pages/example/example',
      success: function (res) { },
      fail: function (res) { }
    }
  },
  getContentList: function (e) {
    let id = "",
      cateType = "",
      category = "",
      that = this,
      tempDataSet = [];
    // datae
    cateType = that.data.cateType;
    category = that.data.category;
    // ,
    if (e) {
      if (e.currentTarget.dataset.id) {
        cateType = e.currentTarget.dataset.type;
      }
      category = e.currentTarget.dataset.category;
    }
    wx.showLoading({
      title: ""
    })
    // ajax
    wx.request({
      url: apiUrl + '/company/caselist?',
      data: {
        id: that.options.id,
        bm:that.options.bm ,
        p: that.data.currentPage,
        pagecount: 15
      },
      header: {
        'content-type': 'application/json'
      },
      success: function (res) {
        for (let i = 0; i < res.data.data.length; i++) {
          res.data.data[i].personNum = app.getPersonNum();
        }
        
        // scroll-view
        that.data.isUpdateScrollHeight ? that.updateScrollHeight(res.data.data.length) : "";
        if (res.data.data.length > 0 && res.data.data.length < 15) {
          that.setData({
            hasData: 0
          });
        } else {
          that.setData({
            hasData: 1
          });
        }
        // articleData
        if (parseInt(res.data.data.length) == 0 && parseInt(that.data.exampleArr.length) == 0) {
          that.setData({
            categoryNoData: 0
          });
        } else {
          that.setData({
            categoryNoData: 1
          });
        }
        tempDataSet = that.data.exampleArr.concat(res.data.data);
        that.setData({
          exampleArr: tempDataSet
        })
        console.log(that.data)
        wx.hideLoading();
      },
      fail: function () {
        console.log("error!!!!");
        wx.hideLoading();
      }
    })
  },
  // 
  loadMoreData: function () {
    let page = this.data.currentPage;
    // 
    if (this.data.hasData) {
      this.setData({
        currentPage: ++page
      });
      this.getContentList();
    }
  },
  toExampleDetail: function (e) {
    let id = e.currentTarget.dataset.id;
    wx.navigateTo({
      url: '../xgt-detail/index?id=' + id + '&type=2&pv=' + app.getPVNum()
    });
  }
})