//index.js
//
const app = getApp();
const navActive = require('../../utils/util.js');
const fadan = require('../../utils/fadan.js');
const apiUrl = app.getApiUrl();
const imgUrl = app.getImgUrl();
let levelOneNav = [{ id: 0, text: "", cate: "shoufang", lc: 89 }, { id: 1, text: "", cate: "gongsi", lc: 90 }, { id: 2, text: "", cate: "shejiyusuan", lc: 91 }, { id: 3, text: "", cate: "xuancai", lc: 92 }, { id: 4, text: "", cate: "chagai", lc: 94 }, { id: 5, text: "", cate: "shuidian", lc: 95 }, { id: 6, text: "", cate: "fangshui", lc: 96 }, { id: 7, text: "", cate: "niwa", lc: 97 }, { id: 8, text: "", cate: "mugong", lc: 98 }, { id: 9, text: "", cate: "youqi", lc: 99 }, { id: 10, text: "", cate: "jianche", lc: 102 }, { id: 11, text: "", cate: "peishi", lc: 103 }, { id: 12, text: "", cate: "baoyang", lc: 104 }, { id: 13, text: "", cate: "jjsh", lc: 141 }];

Page({
  data: {
    userid : "",
    levelOneNav: null,
    levelTwoNav: null,
    currentlevelOneIndex : 0, // 
    currentlevelTwoIndex: 0, // 
    currentPage : 1, //
    articleData: [],
    hasData : 1, //
    categoryNoData : 1, //
    cateType : "", // 
    category : "", // 
    fa: "",
    imgUrl: imgUrl,
    vid : "", //idpv
    scrollContainerHeight : "3600rpx",
    isUpdateScrollHeight: false, //15scrollContainerHeight
    isFixed : false, // ,falsetrue
    scrollTop : "",
    scrollLeft : "", // 
    leftView : "", // id
    lc:''
  },
  onLoad: function (options) {
    let that = this
    wx.getStorage({
      key: 'userId',
      success: function (res) {
        that.setData({
          userid: res.data
        })
      },
      fail: function () {
        app.newSq(that, function (res) {
          if(res===0){
            return
          }
          that.setData({
            userid: res.userId
          })
        })
      }
    })
    //
    fadan.fadan.init(this, 2, {
      cityInput: true,
      addressInput: false,
      phoneInput: true,
      nameInput: true,
      areaInput: false,
      btnText: ""
    });

    // 
    this.setData({
      levelOneNav: levelOneNav
    });

    this.setTab(null,options);

    //this.getContentList();
  },
  // pv
  onShow : function(){
    let viewArticleId = "";
    try {
      viewArticleId = wx.getStorageSync("viewArticleId");
      if (viewArticleId) {
        this.setData({
          vid: viewArticleId
        });
        
        wx.removeStorage({
          key: 'viewArticleId'
        })

        this.updateArticle();
      } 
    } catch (e) {
      console.log(e);
    }
  },
  //
  onPageScroll : function(scrollObj){
  },
  scroll : function(e){
    if (e.detail.scrollTop>2){
      this.setData({
        isFixed : true
      });
    }else{
      this.setData({
        isFixed: false
      });
    }
  },
  // 
  setTab : function(e,options){
    let id = e ? e.currentTarget.dataset.id : options.id, 
      cate = e ? e.currentTarget.dataset.type : options.type,
      category = e ? e.currentTarget.dataset.category : options.category,
      lc = e ? e.currentTarget.dataset.lc : options.lc;
    // id
    if(options){
      this.setData({
        leftView: category,
        currentlevelOneIndex: id,
      });
    }

    if (parseInt(cate)==1){
      //
      this.setData({
        currentlevelOneIndex: id,
        currentlevelTwoIndex : 0
      });
    }else{
      //
      this.setData({
        currentlevelTwoIndex: id
      });
    }
    // 
    this.setData({
      articleData : [],
      currentPage : 1,
      cateType : cate,
      category: category,
      isUpdateScrollHeight : true,
      hasData : 1,
      scrollContainerHeight: "3600rpx",
      lc:lc
    });
    //
    if (parseInt(cate) == 1 && parseInt(id) == 0) {
      this.setData({
        cateType: "",
      });
    }
    wx.pageScrollTo({
      scrollTop: 0
    })
    this.getContentList(e);
  },
  // 
  menu: function (e) {
    // 
    this.setData({
      isHide: true
    });
  },
  cancel: function (e) {
    this.setData({
      isHide: false
    })
  },
  // 
  getContentList : function(e){
    let id = "",
        cateType = "",
        category = "",
        that = this,
        tempDataSet = [],
        lc = that.data.lc;

    // datae
    cateType = that.data.cateType;
    category = that.data.category;
    // ,
    if(e){
      if (e.currentTarget.dataset.id){
        cateType = e.currentTarget.dataset.type;
      }     
    }
    wx.showLoading({
      title : ""
    })
    // ajax
    wx.request({
      url: apiUrl +'/qizuang/liu/cheng?',
      header: {
        'content-type': 'application/x-www-form-urlencoded'
      },
      method: "GET",
      dataType: 'json',
      data : {
        lc:lc,
        p: that.data.currentPage,
        // userid : that.data.userid
      },
      success : function(res){
        // scroll-view
        that.data.isUpdateScrollHeight ? that.updateScrollHeight(res.data.list.length) : "";
        // 015
        if (res.data.list.length > 0 && res.data.list.length < 15 ){
          that.setData({
            hasData : 0
          });
        }else{
          that.setData({
            hasData: 1
          });
        }
        // articleData
        if (parseInt(res.data.list.length) == 0 && parseInt(that.data.articleData.length) == 0){
          that.setData({
            categoryNoData : 0
          });
        }else{
          that.setData({
            categoryNoData: 1
          });
        }
        tempDataSet = that.data.articleData.concat(res.data.list);
        that.setData({
          articleData: tempDataSet
        });
        wx.hideLoading();
      },
      fail:function(){
        wx.hideLoading();
      }
    })
  },
  // pv
  updateArticle : function(){
    let vid = this.data.vid,
        articleList = this.data.articleData,
        pv = "",
        isCollect = 0,
        that = this,
        len = articleList.length;
    wx.request({
      url: apiUrl + '/appletcarousel/details',
      data: {
        userid: that.data.userid,
        id: this.data.vid,
        classtype: '10'
      },
      header: {
        'content-type': 'application/json'
      },
      success : function(res){
        if (res.data.article){
          //pv = res.data.article.pv;
          isCollect = res.data.article.is_collect;
          for(let i=0;i<len;i++){
            if (articleList[i].id == vid){
              //articleList[i].pv = pv;
              articleList[i].pv++;
              articleList[i].is_collect = isCollect;
            }
          }
          that.setData({
            articleData: articleList
          });
        }
      },
      fail : function(){
      }
    })
  },
  // scrollContainerHeight
  updateScrollHeight : function(size){
    if(size<15){
      this.setData({
        scrollContainerHeight : size*240
      });
    }
    this.setData({
      isUpdateScrollHeight: false
    });
  },
  // 
  loadMoreData : function(){
    let page = this.data.currentPage;
    // 
    if (this.data.hasData){    
      this.setData({
        currentPage : ++page
      });
      this.getContentList();
    }
  },
  toArticleDetail : function(e){
    let id = e.currentTarget.dataset.id;
    wx.navigateTo({
      url: '../article-detail/index?id='+id
    })
    //idpv
    wx.setStorage({
      key: 'viewArticleId',
      data: id,
    })
  },
  onShareAppMessage: function (ops) {
    if (ops.from === 'button') {
    }
    return {
      title: '',
      path: 'pages/zhuangxiulc/zhuangxiulc',
      success: function (res) { },
      fail: function (res) { }
    }
  }

})
