//jubu.js
//
const app = getApp();
const navActive = require('../../utils/util.js');
const fadan = require('../../utils/fadan.js');
const apiUrl = app.getApiUrl();
const imgUrl = app.getImgUrl();

Page({
  data: {
    userid: "",
    imgUrl: imgUrl,
    currentPage: 1,
    hasData: 1,
    articleData:[],
    isUpdateScrollHeight: false,
    categoryNoData: 1, 
    scrollContainerHeight: '1455rpx',
  },
  onLoad: function (options) {
    wx.getStorage({
      key: 'userId',
      success: function (res) {
        that.setData({
          userid: res.data
        })
      },
      fail: function () {
        app.newSq(that, function (res) {
          if(res===0){
            return
          }
          that.setData({
            userid: res.userId
          })
        })
      }
    })
    //
    fadan.fadan.init(this, 2, {
      cityInput: true,
      addressInput: false,
      phoneInput: true,
      nameInput: true,
      areaInput: false,
      btnText: ""
    });
    var that= this;
    wx.request({
      url: apiUrl + '/qizuang/xiao/bai?',
      header: {
        'content-type': 'application/x-www-form-urlencoded'
      },
      method: "GET",
      dataType: 'json',
      data: {
        p: that.data.currentPage,
        userid: that.data.userid,
        pagecount: 15,
      },
      success: function (res) {
        that.setData({
          articleData: res.data.list
        });
        wx.hideLoading();
      },
      fail: function () {
        wx.hideLoading();
      }
    })

  },
 
  onShow: function () {
    
  },
  //
  onPageScroll: function (scrollObj) {
  },
  
  // 
  getContentList: function (e) {
    let id = "",
      cateType = "",
      category = "",
      that = this,
      tempDataSet = [];
    // datae
    cateType = that.data.cateType;
    category = that.data.category;
    // ,
    if (e) {
      if (e.currentTarget.dataset.id) {
        cateType = e.currentTarget.dataset.type;
      }
      category = e.currentTarget.dataset.category;
      position = e.currentTarget.dataset.position;
    }
    wx.showLoading({
      title: ""
    })
    // ajax
    wx.request({
      url: apiUrl + '/qizuang/xiao/bai?',
      data: {
        p: that.data.currentPage,
        pagecount: 15,
      },
      header: {
        'content-type': 'application/json'
      },
      success: function (res) {
        // scroll-view
        that.data.isUpdateScrollHeight ? that.updateScrollHeight(res.data.data.length) : "";
        if (res.data.list.length > 0 && res.data.list.length < 15) {
          that.setData({
            hasData: 0
          });
        } else {
          that.setData({
            hasData: 1
          });
        }
        // articleData
        if (parseInt(res.data.list.length) == 0 && parseInt(that.data.articleData.length) == 0) {
          that.setData({
            categoryNoData: 0
          });
        } else {
          that.setData({
            categoryNoData: 1
          });
        }
        tempDataSet = that.data.articleData.concat(res.data.list);
        that.setData({
          articleData: tempDataSet
        });

        wx.hideLoading();
      },
      fail: function () {
        console.log("error!!!!");
        wx.hideLoading();
      }
    })
  },
 
  // 
  loadMoreData: function () {
    let page = this.data.currentPage;
    // 
    if (this.data.hasData) {
      this.setData({
        currentPage: ++page
      });
      this.getContentList();
    }
  },
  //
  toSearch: function () {
    wx.navigateTo({
      url: '../search/index'
    })
  },
  toArticleDetail: function (e) {
    let id = e.currentTarget.dataset.id;
    wx.navigateTo({
      url: '../article-detail/index?id=' + id
    })
    //idpv
    wx.setStorage({
      key: 'viewArticleId',
      data: id,
    })
  },
  toGetScheme: function () {
    wx.navigateTo({
      url: '../zhuangxiusj/zhuangxiusj',
    })
  },
  onShareAppMessage: function (ops) {
    if (ops.from === 'button') {
      // 
      console.log(ops.target)
    }
    return {
      title: '',
      path: 'pages/knowledge-list/knowledge-list',
      success: function (res) { },
      fail: function (res) { }
    }
  }


})
